@keyframes blink {   /* анимация */
    0% {
        left: -130%
    }
    100% {
        left: 130%
    }
}

.default {
    background:goldenrod;
    color: #2b3236;
    outline: none;
}

.red {
    background: indianred;
}

.button {
    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}

.button::after {
    display: block;
    content: '';
    position: absolute;
    left: -130%;
    top:0;
    height: 100%;
    width: 20%;
    transform: skew(45deg);
    background-color: white;
    opacity: 0.7;
    z-index: 14;
}

.button:hover::after {
    animation: blink 0.35s ease;
}

.button:hover::before {
    display: block;
    content: '';
    position: absolute;
    left: -100vw;
    top: 0;
    height: 100vh;
    width: 300vh;
    background-color: white;
    opacity: 0.2;
    z-index: 7;
}

.button:focus{
    outline: #cce4ff solid 1px;
}

.button:active{
    background: aquamarine;
}

.button:disabled{
    color: #2b3236;
    cursor: auto;
}

.button:disabled::after{
    z-index: -1;
}

.button:disabled::before{
    z-index: -1;
}